import { PureComponent, Fragment } from 'react';
import PropTypes from 'prop-types';
import { StatusModal } from '@Green-Dot-Corporation/eureka-ui-modals';

const withSuspensionFlow = (Comp, initCallback) => {
    const getInit = () => {
        return (initCallback && initCallback()) || {};
    };

    return class LoadingFlow extends PureComponent {
        static propTypes = {
            contextCls: PropTypes.string,
        };

        constructor(...args) {
            super(...args);

            this.initProps = getInit();
        }

        state = {
            isSuspenseActive: false,
        };

        render() {
            const { title, ...rest } = this.initProps;
            const { isSuspenseActive } = this.state;
            const modalProps = {
                ...rest,
                ...this.suspenseProps,
                statusText: this.suspenseProps?.statusText || title,
            };

            return (
                <Fragment>
                    {this.renderContent()}
                    <StatusModal {...modalProps} isOpen={isSuspenseActive} />
                </Fragment>
            );
        }

        renderContent = () => {
            return (
                <Comp
                    {...this.props}
                    onSuspensionFinish={this.handleSuspensionFinish}
                    onSuspensionStart={this.handleSuspensionStart}
                />
            );
        };

        handleSuspensionStart = (props = {}) => {
            const { title, ...rest } = props;
            this.suspenseProps = {
                ...rest,
                statusText: title,
            };

            this.setState({
                isSuspenseActive: true,
            });
        };

        handleSuspensionFinish = () => {
            const { isSuspenseActive } = this.state;

            if (isSuspenseActive) {
                this.setState(
                    {
                        isSuspenseActive: false,
                    },
                    () => {
                        this.suspenseProps = {};
                    },
                );
            }
        };

        suspenseProps = {};

        baseCls = 'eureka-suspension-flow-composer';
    };
};

const SuspensionComposer = withSuspensionFlow;

export default SuspensionComposer;
