// ========================================================
// Dependencies
// ========================================================
@import '~@Green-Dot-Corporation/eureka-lib-style-utils/src/styles/utils/unit-conversion-utils.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/mixins/typography.scss';
@import '~@Green-Dot-Corporation/eureka-lib-theme-utils/src/styles/utils/theme-utils.scss';

.eureka-illustration-callout, %eureka-illustration-callout {
    box-sizing: border-box;
    $this: '.eureka-illustration-callout';

    &__msg-container-inner {
        line-height: var(--eureka-core-typography-line-height, eureka-light-theme('core-typography-line-height'));

        > :first-child {
            margin-top: 0;
        }

        > :last-child {
            margin-bottom: 0;
        }
    }

    &__title,
    &__title.eureka-heading,
    &__msg-container-inner {
        @include eureka-max-line-width;
        display: block;
    }

    &__illustration-container {
        line-height: 1;
    }

    .eureka-illustration {
        vertical-align: top;
    }

    &__title-container + &__msg-container {
        margin-top: #{eureka-to-px(eureka-light-theme('core-system-scale') * 2)};
    }

    &__text-container,
    &__illustration-container {
        + .eureka-callout-actions {
            margin-top: #{eureka-to-px(eureka-light-theme('core-system-scale') * 8)};
        }
    }

    &__illustration-container + &__text-container {
        margin-top: #{eureka-to-px(eureka-light-theme('core-system-scale') * 4)};
    }

    &--non-semantic &__title {
        @include eureka-h5;
    }

    &--ill-small &__illustration-container + &__text-container {
        margin-top: #{eureka-to-px(eureka-light-theme('core-system-scale') * 2)};
    }

    &--center-align {
        text-align: center;

        #{$this}__title,
        #{$this}__title.eureka-heading,
        #{$this}__title-container,
        #{$this}__msg-container,
        #{$this}__msg-container-inner {
            margin-left: auto;
            margin-right: auto;
        }
    }

    &:not(&--center-align) {
        .eureka-callout-actions {
            justify-content: flex-start;

            &__inner {
                justify-content: flex-start;
            }
        }
    }
}
