import { put } from '@Green-Dot-Corporation/eureka-lib-fetch-utils';
import { augmentHeaders, handleError, parseError } from './apiUtils';

export default {};

/**
 * after login, server returns accessToken and refreshToken
 * To make a post api call, we need accessToken in the header
 * when accessToken expired, we use refreshToken in the header to get new accessToken
 * when refreshToken expired, we log user out, clear cache and start over
 */
export const REFRESH_TOKEN = 'RefreshToken';
export const ACCESS_TOKEN = 'AccessToken';

const REFRESH_TOKEN_HEADER = 'refresh-token';
const AUTHORIZATION_TOKEN_HEADER = 'Authorization';

export const augment = (options, isLegacy = false) => {
    const defaultHeaders = {};
    const { headers = {}, ...rest } = options ?? {};
    const refreshToken = sessionStorage.getItem(REFRESH_TOKEN);
    const accessToken = sessionStorage.getItem(ACCESS_TOKEN);

    if (refreshToken) {
        defaultHeaders[REFRESH_TOKEN_HEADER] = refreshToken;
    }

    if (accessToken) {
        defaultHeaders[AUTHORIZATION_TOKEN_HEADER] = `Bearer ${accessToken}`;
    }

    return augmentHeaders(
        {
            ...rest,
            headers: { ...defaultHeaders, ...headers },
        },
        isLegacy,
    );
};

/**
 * This is used in post-login scenario. It throws exception if used in pre-login case.
 * @returns Promise -> accessToken
 */
export const fetchAccessToken = async () => {
    const accessToken = sessionStorage.getItem(ACCESS_TOKEN);
    const refreshToken = sessionStorage.getItem(REFRESH_TOKEN);

    if (accessToken && refreshToken) {
        return put('/api/v1/auth/token/accesstoken', augment());
    }

    return undefined;
};

export const authenticatedFetch = async (
    method,
    url,
    options,
    authFetchOptions = {},
) => {
    try {
        const accessToken = sessionStorage.getItem(ACCESS_TOKEN);

        if (!accessToken) {
            throw new Error('Missing accessToken');
        }

        const { headers: otherHeaders, ...rest } = options ?? {};
        const headers = {
            [AUTHORIZATION_TOKEN_HEADER]: `Bearer ${accessToken}`,
            ...otherHeaders,
        };

        return await method(url, { headers, ...rest });
    } catch (error) {
        const errorResult = parseError(error);
        const { statusCode } = errorResult;

        // 401 = accessToken expired
        if (statusCode === 401) {
            try {
                const response = await fetchAccessToken();
                const { accesstoken: accessToken, refreshtoken: refreshToken } =
                    response ?? {};

                if (accessToken && refreshToken) {
                    sessionStorage.setItem(ACCESS_TOKEN, accessToken);
                    sessionStorage.setItem(REFRESH_TOKEN, refreshToken);

                    const { headers, ...rest } = options ?? {};

                    const {
                        [AUTHORIZATION_TOKEN_HEADER]: Authorization,
                        ...otherHeader
                    } = headers ?? {};

                    return authenticatedFetch(method, url, {
                        headers: otherHeader,
                        ...rest,
                    });
                }

                throw new Error('Invalid accessToken');
            } catch (accessError) {
                const accessErrorResult = parseError(accessError);
                sessionStorage.clear();

                return handleError(
                    authFetchOptions.errorHandler,
                    accessErrorResult,
                );
            }
        }

        return handleError(authFetchOptions.errorHandler, errorResult);
    }
};
